@import url(https://fonts.googleapis.com/css?family=Manrope:200,400,700&display=swap);
body {
  font-weight: 300;
  font-family: 'Manrope', sans-serif;
  background-color: #151515;
  min-width: 320px;
}

h1, h2, h3, h4, strong{
  font-weight: 400;
  color: #aaa;
}
p{
  color: #eee;
}

h1 {
  margin-bottom: 30px;
}
/* navbar */
a, a:hover {
  color: inherit;
  text-decoration: none;
}
.navbar{
  min-width: 400px;
}
.navbar img{
  width:100px;
}
.nav-logout{
  cursor:pointer;
}
.navbar-nav{
  width:100%;
  display: flex;
  justify-content: center;
  align-items: center;
  transition: 1s all;
}
/* navbar */


/* Button */


.btn-one {
  color: #FFF;
  transition: all 0.3s;
  position: relative;
  padding: 8px;
}

.btn-one span {
  transition: all 0.3s;
}

.btn-one::before {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  opacity: 0;
  transition: all 0.3s;
  border-top: 1px solid rgba(255, 255, 255, 0.5);
  border-bottom: 1px solid rgba(255, 255, 255, 0.5);
  -webkit-transform: scale(0.1, 1);
          transform: scale(0.1, 1);
}

.btn-one:hover span {
  letter-spacing: 2px;
}

.btn-one:hover::before {
  opacity: 1;
  -webkit-transform: scale(1, 1);
          transform: scale(1, 1);
}

.btn-one::after {
  content: '';
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  z-index: 1;
  transition: all 0.3s;
}

.btn-one:hover::after {
  opacity: 0;
  -webkit-transform: scale(0.1, 1);
          transform: scale(0.1, 1);
}

/* Button */


/* toast */
.toast {
  position: fixed;
  top: 90px;
  right: 50px;
  z-index: 99999;
  width: 350px;
  background-color: #29a744;
  color: white;
}
/* toast */

/* erros msg */
.error-message {
  background: red;
  color: white;
  padding: 10px
}
/* erros msg */
.games-list .card{   
    margin-bottom: 30px;
}

.games-list img {
    height: 160px;
    object-fit: contain;
}
.box{
    overflow: hidden;
}
.box:before{
    content: '';
    position: absolute;
    top: 0;
    left: -250px;
    width:100px;
    height:100%;
    background: rgba(255, 255, 255, .3);
    -webkit-transform: skew(-40deg );
            transform: skew(-40deg );
    transition: .5s all;
}
.box:hover:before {
    left:150%;
}
.modal-content{
    background-color: #29a744;
    color:white;
}
.modal-content label, .modal-content h1{
    color: white;
}
hr{
    border: 1px solid white;
}


.name-and-image{
    display:flex;
    align-items:center;
    margin-bottom: 16px;
}
.user-image{
    width: 50px;
    border-radius: 50%;
    margin: 0 24px 0 0;
}
article{
    margin: 16px;
}
.class-card{
    padding: 0;
    margin-bottom: 32px;
}
.card-event .card-body{
    background-color: #4fd250d6;
    overflow: hidden;
}
.card-event p{
    color: white;
}
.card-zoom {
    margin: 0 1em;
    overflow: hidden;
    transition: -webkit-transform .5s ease;
    transition: transform .5s ease;
    transition: transform .5s ease, -webkit-transform .5s ease;
}
.card-zoom:hover {
    -webkit-transform: scale(1.005);
            transform: scale(1.005);
}
.card-zoom--inverted:hover {
    -webkit-transform: scale(.98);
            transform: scale(.98);
}
.card-zoomed {
    width: 100%;
    height: 100%;
    object-fit: cover;
    transition: .8s ease;
}
.card-zoomed--inverted {
    transition: .5s ease;
}

.card-zoomed:hover {
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.main-section{
    height: 100vh;
    background-image: url(/img/bg.jpeg);
    background-repeat: no-repeat;
    background-size: cover;
    padding: 0 0 0 100px;
    min-width: 320px;
    transition: .3s all;
}

.main-section h1{
    font-size: 4.5em;
    padding: 150px 0 0;
    color: #ddd;
    margin: 0;
    transition: .3s all;
}
.main-section h2{
    font-weight: 100;
    margin-top: 0;
    color: #fff;
    transition: .3s all;
}
.main-section p{
    color: #ccc;
    margin-top: 50px;
    width:400px;
    font-weight: 100;
    line-height: 1.5em;
    word-spacing: 3pt;
    transition: .3s all;
}

.fadeIn {
    -webkit-animation-duration: 1s;
            animation-duration: 1s;
    -webkit-animation-delay: .2s;
            animation-delay: .2s;
    -webkit-animation-fill-mode: both;
            animation-fill-mode: both;
    -webkit-animation-name: fadeIn;
            animation-name: fadeIn;
}

@-webkit-keyframes fadeIn {
    from {
        -webkit-transform: translateX(100px);
                transform: translateX(100px);
        opacity: 0;
    }

    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
}

@keyframes fadeIn {
    from {
        -webkit-transform: translateX(100px);
                transform: translateX(100px);
        opacity: 0;
    }

    to {
        -webkit-transform: translateX(0);
                transform: translateX(0);
        opacity: 1;
    }
}

@media (max-width: 600px){
    .main-section{
        
        padding: 0 24px;
        min-width: 320px;
    }
    .main-section h1{
        padding: 32px 0 0 0;
    }
    .main-section p {
        max-width: 480px;
    }
}

@media (max-width: 414px) {
    .main-section h1 {
        font-size: 3em;
        padding-top: 32px;
    }

    .main-section h2 {
        font-size: 1.3em;
    }

    .main-section p {
        width: 380px;
    }
}

@media (max-width: 375px){
    .main-section h1{
        font-size: 3em;
        padding-top: 32px;
    }
    .main-section h2{
        font-size: 1.3em;
    }
    .main-section p{
        width: 330px;
    }
}
@media (max-width: 320px) {
    main{
        max-width: 320px;
    }
    .main-section h1 {
        font-size: 3em;
        padding-top: 32px;
    }

    .main-section h2 {
        font-size: 1.3em;
    }

    .main-section p {
        width: 280px;
    }
}
